nav {
  background-color: #E7E8EC;
  margin: 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 3px 3px 1px rgba(0, 0, 0, 0.1);
  height: 100px; }
  nav ul {
    margin: 0;
    padding: 0; }
  nav .logo-container {
    width: 25%; }
  nav .item-container {
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  nav .button-container {
    width: 25%; }

.underline-effect {
  width: 0%;
  height: 4px;
  border-radius: 100px;
  background-color: #8EC04F; }

.nav-item {
  text-decoration: none;
  color: #383F45;
  font-family: 'Teko', sans-serif;
  text-transform: uppercase;
  font-size: 26px;
  -webkit-transition: ease 0.4s;
  transition: ease 0.4s; }

.nav-item-container:hover .nav-item {
  color: #383F45; }

.nav-item-container:hover .underline-effect {
  -webkit-animation: underlineMe 0.5s;
          animation: underlineMe 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

.nav-item-container .underline-effect {
  -webkit-animation: unUnderlineMe 0.5s;
          animation: unUnderlineMe 0.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

.nav-item-container {
  width: 15%;
  float: left;
  padding: 0px 10px; }

@-webkit-keyframes underlineMe {
  0% {
    width: 0%; }
  100% {
    width: 100%; } }

@keyframes underlineMe {
  0% {
    width: 0%; }
  100% {
    width: 100%; } }

@-webkit-keyframes unUnderlineMe {
  0% {
    width: 100%; }
  100% {
    width: 0%; } }

@keyframes unUnderlineMe {
  0% {
    width: 100%; }
  100% {
    width: 0%; } }

.active {
  color: #71993f; }

.active ~ .underline-effect {
  -webkit-animation: underlineMe 0.01s;
          animation: underlineMe 0.01s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

