.slider-container {
  background-color: #383F45;
  width: 100%;
  height: 350px; }

.slideshow {
  overflow: hidden;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 4fr 4fr 1fr;
  grid-template-rows: repeat(3, 1fr);
  grid-template-areas: '. slide slide .' 'goback slide slide goforward' '. bottombuttons pauseplay image'; }

.slide-four,
.slide-one,
.slide-three,
.slide-two {
  width: 100%;
  grid-area: slide; }

.slide-text {
  padding: 0;
  margin: 0;
  grid-area: text;
  margin-top: 10px; }

.pause-play-icons,
.step-icons {
  background-color: transparent;
  border: none;
  color: white;
  font-size: 24px;
  outline: 0 !important;
  cursor: pointer; }

.step-icons {
  font-size: 48px; }

.pause-play-container {
  grid-area: pauseplay;
  align-self: center; }

.step-icon-back {
  grid-area: goback;
  justify-self: center;
  align-self: flex-start; }

.step-icon-forward {
  grid-area: goforward;
  justify-self: center;
  align-self: flex-start; }

.bottom-button-container {
  grid-area: bottombuttons;
  justify-self: flex-end;
  align-self: center; }

.slider-button-control {
  background-color: transparent;
  border: none;
  color: white;
  font-size: 18px;
  padding: 3px;
  outline: 0;
  cursor: pointer; }

